@import '../../global/shared.scss';

.container {
  width: 960px;
  margin: 0 auto;
}

@media screen and (max-width: $large-screen) {
  .container {
    width: 720px;
  }
}

@media screen and (max-width: $medium-screen) {
  .container {
    width: 540px;
  }
}

@media screen and (max-width: $xsmall-screen) {
  .container {
    width: 100%;
  }
}
